.index {
    padding: 1.6rem 0;

    .index-header {
        color: #fff;

        span {
            font-weight: 600;
            font-size: 2rem;
        }

        .base-style {
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .win {

            background-color: #70ca63;
        }

        .linux {
            background-color: #967adc;
        }

        .java {
            background-color: #5d5dfd;
        }

        .net {
            background-color: #fb452d;
        }

        .icon-style {
            color: #fbfbf4b3;
            font-size: 55px;
            padding-right: 1rem;
        }
    }

    .bar-header {
        display: flex;
        justify-content: space-between;
    }
}